.logo{
    color: azure;
    font-size: 30px;
}


.Navbar{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 10px 20px;
    margin: 0;
}

.Navbar {
    color: azure;
    font-size: 25px;
}

a{
    text-decoration: none;
    color: azure;
    transition: color 0.25s;
}

.navLinks a:hover{
    color: #039BE5;
}

ul{
    margin: 0;
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

li{
    max-height: 35px;
    padding: 0 20px;
