}

.shirt:hover, .suit:hover, .pant:hover{
    cursor: pointer;
}

.shirt{
    position: relative;
    background-image: url(/djastailors/static/media/shirt-home.b1e72d01.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    justify-content: space-between;
    height: 600px;
    width: 420px;
    font-size: 35px;
    color: white;
    text-shadow: 1px 1px 1px #303030;
    padding: 20px 0px;
    margin-bottom: 50px;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
    overflow: hidden;
}

.shirt:hover{
    -webkit-transform: scale(110%);
            transform: scale(110%);
}

